                position: absolute;
            }
        }

        &__03 {
            right: -11px;
            bottom: 510px;

            &::before {
                left: -25px;
                top: 6px;
            }
        }
    }
}

.section-stock {
    background-image: url(../img/bg-2.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 100px;

    &__text {
        font-size: 1.5rem;
        max-width: 690px;
        margin-top: 3.75rem;
        line-height: 1.5;
    }
}

.slider {
    margin-top: 92px;

    &-child {
        padding: 5px 10px;
        box-sizing: border-box;

        &:focus {
            outline: none;
        }
    }

    &-item {
        border: 1px solid rgba($gray, $alpha);
        border-radius: 20px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        min-height: 520px;
        position: relative;
        padding: 2rem 2.25rem;
        box-sizing: border-box;
        box-shadow: 0 0 0 2px rgba($gray, 0);
        transition: 0.3s ease;
        overflow: hidden;
        &:hover {
            box-shadow: 0 0 0 2px rgba($gray, $alpha);
        }

        &:hover {
            .slider-text {
                transform: translate(0, 0);
            }
        }
    }

    &-hover {
        transition: 0.3s ease;
        overflow: hidden;
        padding: 2rem 2.25rem;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 20px;
            transition: 0.3s ease;
        }
        &:hover::before {
            background-color: rgba($white, 0.55);
        }
        &-milk {
           background-image: url(../img/milk.png);
        }
        &-text {
            position: absolute;
            bottom: 2.75rem;
            left: 2.25rem;
            max-width: 58%;
            transform: translate(0, 241px);
            transition: 0.3s ease;

            &__title {
                font-size: 1.8rem;
                font-family: 'Cormorant', serif;
                margin-bottom: 26px;
            }

            &__description {
                color: rgba($gray, 0.7);
                margin-bottom: 41px;
                line-height: 1.5;
            }

            &__link {
                color: $gray;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: 500;
                letter-spacing: 0.075rem;
                opacity: 0.8;
                position: relative;
                padding: 50px;
                transition: 0.3s ease;

                &:hover {
                    opacity: 1;
                    padding: 45px;
                }

                &:focus {
                    outline: none;
                }

                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: calc(50% - 0.93rem);
                    display: block;
                    width: 1.875rem;
                    height: 1.875rem;
                    background-image: url(../img/plus.svg);

                }
            }
        }
    }

