    .slick-prev,
    .slick-next {
        position: absolute;
        background-color: transparent;
        border: none;
        top: -63px;
        text-indent: -9999px;
        background-repeat: no-repeat;
        background-size: contain;
        display: block;
        width: 10px;
        height: 18px;
        opacity: 0.4;
        transition: 0.2s ease;
        cursor: pointer;

        &:hover {
            opacity: 1;
        }

        &:focus {
            outline: none;
        }
    }

    .slick-next {
        right: 0;
        background-image: url(../img/next.svg);
    }

    .slick-prev {
        right: 80px;
