    .section-new {
        background-image: url(../img/bg-4.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        padding-bottom: 208px;
    }

    .slider-thumb {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 314px;
        margin-bottom: 40px;

        &__image {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }



    .slider-info {
        &__title {
            font-size: 1.87rem;
            font-family: 'Cormorant', serif;
            margin-bottom: 26px;
        }

        &__description {
            font-size: 1.12rem;
            color: rgba($gray, 0.7);
            line-height: 1.5;
            margin-bottom: 0;
            min-height: 52px;
            max-width: 96%;
        }

        &__price {
            font-size: 1.5rem;
            margin-top: 28px;
        }
    }

    .slider-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 32px;
        margin-bottom: 8px;

        &__btn {
            border: none;
            padding: 1.3rem 2rem;
            cursor: pointer;
            font-size: 0.87rem;
        }
    }


.slider-count {
    display: flex;
    align-items: center;
    font-size: 1.5rem;

    &__minus,
    &__plus {
        display: block;
        width: 3.12rem;
        height: 3.12rem;
        background-repeat: no-repeat;
        background-size: cover;
        cursor: pointer;
        opacity: 0.4;
        transition: 0.2s ease;

        &:hover {
            opacity: 1;
        }
    }

    &__number {
        border: none;
        width: 40px;
        text-align: center;
    }

    &__minus {
        background-image: url(../img/minus.svg);
    }

    &__plus {
        background-image: url(../img/plus.svg);
    }
}